body {
  font-family: Authentic, sans-serif;
  font-weight: 60;
  font-size: 11pt;
}

.pagedjs_page_content {
  orphans: 2;
  widows: 2;
}

h2 {
  font-family: Louise-Regular, sans-serif;
  font-size: 64pt;
  text-align: center;
}

h3 {
  font-family: Authentic, sans-serif;
  font-weight: 150;
  text-align: center;
}
h3:first-child {
  margin-top: 0;
  padding-top: 0;
}

h5 {
  text-align: center;
  font-size: 11pt;
}

blockquote {
  margin-left: 1cm;
  margin-right: 1cm;
}
blockquote > p {
  margin: 0;
  padding: 0;
  text-indent: 0;
}

sup,
.footnote {
  font-family: Authentic, sans-serif;
  font-weight: 150;
  vertical-align: top;
  position: relative;
  top: -0.2em;
  font-size: 8pt;
}
.full-spread-image-caption,
.thumbcaption {
  font-family: Authentic, sans-serif;
  font-weight: 90;
  text-align: center;
}

.full-spread-image-caption {
  line-height: 8pt;
  text-align: left;
}

/* we have to disable the baseline grid margin/padding 
	that Plumber sets above for the first element on a page */
.chapter > p:first-child {
  margin-top: 0;
  padding-top: 0;
}

.pagedjs_margin {
  font-family: Authentic, sans-serif;
  font-weight: bold;
  font-size: 12pt;
}

.pagedjs_margin-bottom-left-corner-holder .pagedjs_margin-content,
.pagedjs_margin-bottom-right-corner-holder .pagedjs_margin-content {
  font-family: Louise-Regular, sans-serif;
}

/* 
Some style to help debugging PagedJs, layer order etc. 
*/
@media screen {
  .debug {
    /*
    For debugging the baseline grid append ?debug to the url 
    */
    /*
    	reset z-index on .pagedjs_area so right click works
    */
  }
  .debug .pagedjs_page_content {
    background-image: repeating-linear-gradient(to bottom, transparent 0px, transparent 11.25pt, rgba(255, 0, 0, 0.1) 11.25pt, rgba(255, 0, 0, 0.1) 12pt);
  }
  .debug .pagedjs_layout-title_page .pagedjs_area {
    z-index: unset;
  }
}
h2 {
  break-before: right;
}

.chapter {
  break-before: left;
}

.title-image-wrap {
  page-break-before: left;
}

.full-spread-image-section {
  page-break-before: left;
}

.full-page-image {
  page-break-before: always;
}

.full-page-image.full-page-image-left {
  page-break-before: left;
}

.full-page-image.full-page-image-right {
  page-break-after: always;
}

.full-page-image {
  width: var(--pagedjs-width);
  height: var(--pagedjs-height);
  overflow: hidden;
  left: calc((var(--pagedjs-bleed-left) + var(--pagedjs-margin-left) + var(--pagedjs-padding-left)) * -1);
  top: calc((var(--pagedjs-bleed-top) + var(--pagedjs-margin-top) + var(--pagedjs-padding-top)) * -1);
  position: absolute;
}

.full-page-image div {
  width: calc(var(--pagedjs-width) * 2 - var(--pagedjs-bleed-left) - var(--pagedjs-bleed-right));
  height: var(--pagedjs-height);
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

/* compensate for bleeds */
.pagedjs_right_page .full-page-image div {
  margin-left: calc(var(--pagedjs-width) * -1 - var(--pagedjs-bleed-left) * 2 + var(--pagedjs-bleed-right) * 2);
}

.full-page-image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  margin: 0 !important;
}

.title-spread .full-page-image {
  background-color: black;
}

.full-spread-image-section .full-page-image div.full-spread-image-caption {
  height: auto;
  position: absolute;
  display: block;
  right: calc(var(--pagedjs-bleed-right) + var(--pagedjs-margin-right));
  bottom: calc(var(--pagedjs-bleed-bottom) + var(--pagedjs-margin-bottom));
  width: calc(var(--pagedjs-width) - var(--pagedjs-bleed-right) - var(--pagedjs-margin-right) - var(--pagedjs-bleed-left) - var(--pagedjs-margin-left));
}

/* General styling */
html {
  font-size: 62.5%;
}

body {
  color: var(--color-main);
  margin: 0;
  padding: 0;
}

img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
}

h3 + p {
  text-indent: 0 !important;
}

.pagedjs_pages .chapter.invert-type h2 + p {
  text-indent: 0 !important;
}

/*
Setting named-pages
https://pagedjs.org/documentation/8-named-page/
*/
ul {
  list-style-image: url('data:image/svg+xml;utf8,<svg viewBox="0 0 40 40" xmlns="http://www.w3.org/2000/svg"><circle style="fill: none; stroke: rgb(0, 0, 0);" cx="20" cy="20" r="18" stroke-width="2"/></svg>');
}
ul ::marker {
  font-size: 3rem;
}

.empty-page {
  page: empty-page !important;
}

/* Prevent superscript from messing up the line-height */
sup {
  vertical-align: top;
  position: relative;
  top: -0.5em;
}

a {
  text-decoration: none;
  color: black;
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word;
}

h2 {
  overflow-wrap: break-word;
  word-wrap: break-word;
  word-break: break-word;
}

/* Generic css */
/* Show url of a tags */
/* a::after {
content: " (" attr(href) ")";
} */
a.image {
  display: block;
  text-align: center;
}

.pagedjs_pages .pagedjs_area .chapter .image-container {
  margin: 0;
  padding: 0;
  text-indent: 0;
  text-align: center;
}
.pagedjs_pages .pagedjs_area .chapter .image-container a {
  display: block;
}

/* thumbnails, keep caption and image on same page */
.thumb {
  break-inside: avoid;
}

.thumbinner {
  margin: 0 auto;
  width: auto !important;
}

/* mediawiki gives thumbs an inline fixed width */
.thumbinner.image-container {
  width: auto !important;
}

.contributors {
  display: none;
}

.pagedjs_layout-title_page .pagedjs_area {
  z-index: -1;
}

.title-image-wrap {
  /* weird swapping of values because of the transform gives me headache */
}
.title-image-wrap .image {
  position: absolute;
  height: calc(var(--pagedjs-height) - var(--pagedjs-bleed-right) - var(--pagedjs-bleed-left));
  overflow: hidden;
  left: calc((var(--pagedjs-margin-left) + var(--pagedjs-padding-left)) * -1);
  right: calc((var(--pagedjs-margin-right) + var(--pagedjs-padding-right)) * -1);
  top: calc((var(--pagedjs-margin-top) + var(--pagedjs-padding-top)) * -1);
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
.title-image-wrap .image span.bg {
  width: 100%;
  background-color: yellow;
}
.title-image-wrap .image img {
  max-width: 100%;
  max-height: 100%;
  height: auto;
  mix-blend-mode: multiply;
  display: block;
}
.title-image-wrap .thumbcaption {
  overflow: hidden;
  position: absolute;
  height: calc(var(--pagedjs-pagebox-width) / 2);
  width: calc(var(--pagedjs-height) - var(--pagedjs-bleed-right) - var(--pagedjs-bleed-left));
  left: calc((var(--pagedjs-margin-left) + var(--pagedjs-padding-left)) * -1);
  top: calc(var(--pagedjs-height) - var(--pagedjs-bleed-top) * 2 - var(--pagedjs-margin-top) - var(--pagedjs-padding-top));
  transform: rotate(-90deg);
  transform-origin: top left;
  padding: 20pt 80pt;
  font-family: Sligoil-Micro, sans-serif;
  font-size: 9pt;
}

.print-ui {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2147483647;
  display: block;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
.print-ui .can-click {
  pointer-events: all;
}

.print-ui-element {
  display: block;
  margin: 10px 0;
}

.ui {
  background-color: #fff;
  box-shadow: 0 1px 1px transparent;
  overflow: hidden;
  padding: 20px 10px 10px;
  position: absolute;
  transition: left 0.1s ease, box-shadow 2s ease-out, transform 0.3s ease;
  width: 50%;
  top: 50%;
  left: calc(-50% - 20px);
  transform: translate(0%, -50%);
  z-index: 10000;
  background: yellow;
  font-size: 5rem;
  font-family: le-murmure, sans-serif;
  color: blue;
}
.ui input[type=checkbox] {
  width: 5rem;
  height: 5rem;
  margin: 2rem;
}
.ui input:has(input::checked) {
  font-family: Authentic, sans-serif;
  font-weight: 90;
}

input:checked ~ .ui {
  left: 50%;
  box-shadow: 0 0 20px rgb(0, 0, 255);
  transform: translate(-50%, -50%);
}

/* From: https://codepen.io/varystrategic/pen/YPMOVO */
.show_hide ~ label .menu__icon {
  pointer-events: all;
  display: inline-block;
  position: relative;
  margin: calc(1rem + 0.6em);
  width: 3em;
  height: 3em;
  background: whitesmoke;
  border-radius: 50%;
  border: 0.8571428571em solid blue;
  box-sizing: border-box;
  transition: border 0.6s ease;
}
.show_hide ~ label .menu__icon:before, .show_hide ~ label .menu__icon:after {
  content: "Ã—";
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: "times new roman";
  font-weight: bold;
  font-size: 6.6em;
  color: blue;
  transition: border 0.3s ease, color 0.3s ease;
}
.show_hide ~ label .menu__icon:after {
  transform: translate(-50%, -50%) rotate(45deg);
}

.show_hide {
  display: none;
}

.show_hide:checked ~ label .menu__icon {
  border-width: 0px;
  transition: border 0.6s ease;
  background-color: transparent;
}
.show_hide:checked ~ label .menu__icon:after {
  color: transparent;
}
.show_hide:checked ~ label .menu__icon:before {
  z-index: 1;
}

.toc-link {
  font-size: 4rem;
  position: absolute;
  top: 2rem;
}

.hide_foreground .pagedjs_pagebox *, .hide_foreground .pagedjs_pagebox *::after, .hide_foreground .pagedjs_pagebox *::before, .hide_foreground .pagedjs_pagebox a {
  color: transparent !important;
}
.hide_foreground .pagedjs_pagebox img {
  opacity: 0;
}
.hide_foreground .pagedjs_pagebox .bg {
  background-color: black;
}
.hide_foreground .pagedjs_pagebox img.title_image {
  opacity: 1;
  filter: brightness(0);
}

.hide_background .pagedjs_pagebox * {
  background-color: transparent !important;
}
.hide_background .p5Canvas {
  display: none !important;
}
.hide_background .bg {
  background-color: transparent !important;
}